import React, { Component } from 'react';
import { getStudentGrade } from 'services/educationPortal';
import to from 'await-to-js';
import {
  Divider,
  Table,
  Row,
  Col,
} from 'antd';
import {
  getQueryParams
} from 'utils/urlUtil';

class TableRoomsForAssignSubject extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      data: [],
      currentRecord: {},
      calculateTotalGradeNote: [],
    };
  }

  componentDidMount = () => {
    const { studentId } = getQueryParams();
    if (studentId) {
      this.getGradeStudent(studentId);
    }
  };

  // componentWillReceiveProps() {
  //   const { studentIdNew } = getQueryParams();
  //   const { studentId } = this.state;
  //   if (studentId !== studentIdNew) {
  //     this.setState({
  //       studentId: studentIdNew,
  //     });
  //     this.getGradeStudent(studentIdNew);
  //   }
  // }

  onClickOnRowTable = record => {
    this.props.chooseRoom(record);
    this.setState({
      currentRecord: record,
    });
  };

  getGradeStudent = async (studentId) => {
    this.setState({ loading: true });
    const [error, response] = await to(getStudentGrade({ studentId }));
    console.log(response);
    this.setState({ loading: false });
    if (error) return;
    const data = response[0].data.items

    // calculateGradeNote
    const calculateGradeNote = {};
    data.forEach(yearEdu => {
      if (yearEdu.semesters) {
        yearEdu.semesters.forEach(semester => {
          if (semester.subjectGrades) {
            semester.subjectGrades.forEach(subject => {
              if (calculateGradeNote[subject.gradeNote]) {
                calculateGradeNote[subject.gradeNote] += 1;
              } else {
                calculateGradeNote[subject.gradeNote] = 1;
              }
            })
          }
        })
      }
    })
    this.setState({
      data,
      calculateTotalGradeNote: [calculateGradeNote],
    });
  };

  setRowClassName = record => {
    const { currentRecord } = this.state;

    if (record.id === currentRecord.id) {
      return 'row-group-type';
    }
    return '';
  };


  expandedGradePartsRowRender = subjectGrades => {
    const innerColumns = [
      {
        title: 'Lần kiểm tra',
        dataIndex: 'label',
        key: 'label',
        className: 'subjectGrades-label',
      },
      {
        title: 'Điểm',
        dataIndex: 'grade',
        key: 'grade',
      },
      {
        title: 'Tỉ lệ',
        dataIndex: 'ratio',
        key: 'ratio',
      },
    ];

    return (
      <Table
        columns={innerColumns}
        dataSource={subjectGrades.gradeParts || []}
        pagination={false}
        // expandedRowRender={this.expandedGradePartsRowRender}
        className="subjectGrades-table"
        rowKey="buildingFloor"
      />
    );
  };

  expandedSubjectGradeRowRender = semester => {
    const innerColumns = [
      {
        title: 'Môn học',
        dataIndex: 'subject',
        key: 'subject',
        className: 'subjectGrades-label',
      },
      {
        title: 'Số lượng chứng chỉ',
        dataIndex: 'noOfCredit',
        key: 'noOfCredit',
      },
      {
        title: 'Điểm',
        dataIndex: 'grade',
        key: 'grade',
      },
      {
        title: 'Điểm tổng kết',
        dataIndex: 'finalGrade',
        key: 'finalGrade',
      },
      {
        title: 'Kết quả',
        dataIndex: 'gradeNote',
        key: 'gradeNote',
      },
    ];

    return (
      <Table
        columns={innerColumns}
        dataSource={semester.subjectGrades || []}
        pagination={false}
        expandedRowRender={this.expandedGradePartsRowRender}
        className="subjectGrades-table"
        rowKey="buildingFloor"
      />
    );
  };

  expandedSemesterRowRender = yearEdu => {
    const innerColumns = [
      {
        title: 'Học kỳ',
        dataIndex: 'semester',
        key: 'semester',
        className: 'semester',
      },
    ];

    return (
      <Table
        columns={innerColumns}
        dataSource={yearEdu.semesters || []}
        pagination={false}
        expandedRowRender={this.expandedSubjectGradeRowRender}
        className="semesters-table"
        rowKey="buildingFloor"
      />
    );
  };

  render() {

    const outerColumns = [
      {
        title: 'Năm học',
        dataIndex: 'yearEdu',
        key: 'yearEdu',
        className: 'yearEdu',
      },
    ];

    const totalColumns = [];

    if (this.state.calculateTotalGradeNote && this.state.calculateTotalGradeNote[0]) {
      Object.keys(this.state.calculateTotalGradeNote[0]).forEach(key => {
        console.log(this.state.calculateTotalGradeNote[0][key]);
        totalColumns.push({
          title: key,
          dataIndex: key,
          key,
        });
      })
    }

    return (
      <div>
        <Divider>Chi tiết</Divider>
        <Table
          loading={this.state.loading}
          className="grade-student-view-table"
          columns={outerColumns}
          expandedRowRender={this.expandedSemesterRowRender}
          dataSource={this.state.data || []}
          rowKey="building"
          pagination={false}
          bordered
        />
        <Divider>Tổng kết</Divider>
        <Row>
          <Col span={18} push={3}>
            <Table
              loading={this.state.loading}
              className="grade-student-view-total-table"
              columns={totalColumns}
              dataSource={this.state.calculateTotalGradeNote || []}
              rowKey="gradeNote"
              pagination={false}
              bordered
            />
          </Col>
        </Row>

      </div>
    );
  }
}

export default TableRoomsForAssignSubject;



