import React, { Component } from 'react';
import {
  Card,
  Row,
  Col,
  Form,
  message,
} from 'antd';
import { BtnExport } from 'components/common/button';
// import { requiredRule } from 'utils/validateRules';
import Page from 'components/LayoutComponents/Page';
import { getQueryParams } from 'utils/urlUtil';
import {
  exportStudentGradeExcel
} from 'services/educationPortal';
import { objectEquals } from 'utils/functionUtil';
import TableGradeStudentView from './TableGradeStudentView';
import './style.scss';

@Form.create({ name: 'ClassOwnerActivityForm' })
class ClassOwnerActivity extends Component {
  constructor(props) {
    super(props);
    this.state = {
      studentId: null,
    };
  }

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = nextProps => {
    if (!objectEquals(nextProps.params, this.props.params)) {
      this.handleChangeQueryParams();
    }
  };

  handleChangeQueryParams = async () => {
    const { studentId } = getQueryParams();
    this.setState({
      studentId,
    });
  };

  exportGradeStudentView = async () => {
    const { studentId } = this.state;
    try {
      await exportStudentGradeExcel({ studentId });
    } catch (error) {
      message.error(error);
    }
  };

  render() {
    return (
      <Card title="Xem điểm sinh viên" className="class-owner-activity">
        <Row>
          <Col span={24} style={{ textAlign: 'right' }} >
            <BtnExport onClick={() => this.exportGradeStudentView()} />
          </Col>
        </Row>
        <TableGradeStudentView />
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ClassOwnerActivity />
  </Page>
);



